.calc-slider {
    &-range {
        width: 100%;
        position: relative;
    }
    &-range::before {
        content: '0 %';
    }
    &-range::after {
        content: '100 %';
        right: 0;
    }
    &-range::before,
    &-range::after {
        position: absolute;
        top: 25px;
    }
    label {
        display: block;
        margin-bottom: 10px;
        color: $mm-dark-primary-color;
    }
}


/* Ostylowanie pola input typu range by http://danielstern.ca/range.css/#/ */

.calc-slider {
    input[type=range] {
        -webkit-appearance: none;
        width: 100%;
        margin: 5.55px 0;
    }
    input[type=range]:focus {
        outline: none;
    }
    input[type=range]::-webkit-slider-runnable-track {
        width: 100%;
        height: 15.9px;
        cursor: pointer;
        background: #000000;
        border-radius: 0px;
        border: 0px solid #010101;
    }
    input[type=range]::-webkit-slider-thumb {
        height: 27px;
        width: 14px;
        border-radius: 5px;
        background: rgba(255, 160, 0, 0.93);
        cursor: pointer;
        -webkit-appearance: none;
        margin-top: -5.55px;
    }
    input[type=range]::-moz-range-track {
        width: 100%;
        height: 15.9px;
        cursor: pointer;
        background: #000000;
        border-radius: 0px;
        border: 0px solid #010101;
    }
    input[type=range]::-moz-range-thumb {
        height: 27px;
        width: 14px;
        border-radius: 5px;
        background: rgba(255, 160, 0, 0.93);
        cursor: pointer;
    }
    input[type=range]::-ms-track {
        width: 100%;
        height: 15.9px;
        cursor: pointer;
        background: transparent;
        border-color: transparent;
        color: transparent;
    }
    input[type=range]::-ms-fill-lower {
        background: #000000;
        border: 0px solid #010101;
        border-radius: 0px;
    }
    input[type=range]::-ms-fill-upper {
        background: #000000;
        border: 0px solid #010101;
        border-radius: 0px;
    }
    input[type=range]::-ms-thumb {
        height: 27px;
        width: 14px;
        border-radius: 5px;
        background: rgba(255, 160, 0, 0.93);
        cursor: pointer;
        height: 15.9px;
    }
    input[type=range]:focus::-ms-fill-lower {
        background: #000000;
    }
}
